
body{
  margin:0px;
}
.title{
  position: fixed;
  top:20px;
  width: 100%;
  text-align: center
}
.title .main{
  font-family: 'Josefin Sans', sans-serif;
  font-size: 30px;
  font-weight: 900;
  -webkit-text-stroke: 1px black;
  padding: 5px;
}
.title .date{
  font-family: 'Josefin Sans', sans-serif;
  font-size: 15px;
  font-weight: 900;
  padding: 5px;
}
.button-area{
  position: fixed;
  bottom: 30px;
  width: 100%;
}
.button-area .red-button{
  display: block;
  margin: auto;
  color: #ffe0da;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 18px;
  background-color: #F64850;
  padding: 5px 70px;
  border-radius: 30px;
  border: solid 1px #e5b9b8;
  font-weight: 900;
  margin-bottom: 5px;
}
.button-area .text{
  text-align: center;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 18px;
  font-weight: 900;
  padding: 5px;
  color: #868686;
}
.progress{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.percent-text{
  font-family: 'Josefin Sans', sans-serif;
  color: black;
}
